      }
    
      .table-responsive {
        margin-top: 0;
      }
    
      .student-table img {
        max-width: 30px; /* Significantly smaller image size for very small screens */
        max-height: 30px;
      }
  
      .student-table img.student-image {
          max-width: 30px;
        }
  
        .heading {
